/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect, useRef } from 'react';
import * as PatientSlice from 'store/patient/shared/slice';
import * as fromPatientSelector from 'store/patient/shared/selectors';
import * as LocationSelectors from 'store/location/shared/selectors';
import * as _ from 'lodash';
import { PatientSaga } from 'store/patient/shared/saga';
import { useInjectReducer, useInjectSaga } from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { useForm } from 'react-hook-form';
import { LocationTypes } from 'store/patient/constants/http.constant';
import { DEFAULT_FORMAT_INPUT_DATE } from 'store/common/constants/common.constant';
import { PatientModel } from 'store/patient/shared/types';
import { PatientService } from 'services/patient.service';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DateFnsUtils from '@date-io/date-fns';
import '../../PersonnelPage/Modal.css';

export function ModalPatient({ open, onClose }) {
  useInjectReducer({ key: PatientSlice.sliceKey, reducer: PatientSlice.reducer });
  useInjectSaga({ key: PatientSlice.sliceKey, saga: PatientSaga });
  const { register, handleSubmit, errors, setValue, getValues } = useForm();
  const dispatch = useDispatch();
  const dateUtil = new DateFnsUtils();
  const locations: any[] = useSelector(LocationSelectors.selectLocations);
  const patientData: any = useSelector(fromPatientSelector.selectDataPatient);
  const [province, setProvince] = useState<any>({});
  const [dist, setDist] = useState<any>({});
  
  useEffect(() => {
    return () => {
      dispatch(PatientSlice.actions.clearPatientInfo());
    }
  }, []);

  useEffect(() => {
    async function initFormValue(patient) {
      for (let key in patient) {
        if ((key === 'birthday' || key === 'identity_card_number_issued_date' || key === 'health_insurance_expired_date' || key === 'health_insurance_issued_date' || key === 'identity_card_number_issued_date') && patient[key]) {
          const date = dateUtil.format(
            new Date(patient[key]),
            DEFAULT_FORMAT_INPUT_DATE
          );
          await setValue(key, date);
        } else if (key === 'province_id') {
          const initProvince: any = locations.find(
            loc => loc.province_code === patient.province_id,
          );
          if (initProvince) {
            setValue('province_id', initProvince.province_code);
            const distInit = initProvince.districts.find(
              dis => dis.district_code === patient.district_id,
            );
            if (distInit) {
              setDist(distInit);
              await setProvince(initProvince);
              setValue('district_id', distInit.district_code);
              const wardInit = distInit.wards.find(
                ward => ward.ward_code === patient.ward_id,
              );
              if (wardInit) {
                setValue('ward_id', wardInit.ward_code);
              }
            }
          }
        } else {
          setValue(key, patient[key]);
        }
      }
    }
    if (!_.isEmpty(patientData)) {
      setTimeout(() => initFormValue(patientData));
    }
  }, [patientData]);

  const onSubmit = form => {
    const editPatientData = Object.assign({}, form, {
      code: patientData.code,
      tekmedi_id: patientData.tekmedi_id,
      tekmedi_card_number: patientData.tekmedi_card_number,
      id: patientData.id,
    });
    dispatch(PatientSlice.actions.updatePatient(editPatientData));
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'md'}
      open={open}
      onClose={onClose}
      aria-labelledby="max-width-dialog-title"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="row">
            <div className="form-group col-md-12">
              <h3 className="m-0">Cp Nht Thng Tin Bnh Nhn</h3>
            </div>
            <div className="form-group col-md-6">
              <label>M th</label>
              <input
                placeholder="M th"
                type="text"
                className="form-control"
                name="tekmedi_card_number"
                disabled
                ref={register()}
              />
            </div>
            <div className="form-group col-md-6">
              <label>M bnh nhn</label>
              <input
                type="text"
                className="form-control"
                name="code"
                disabled
                placeholder="M bnh nhn"
                ref={register({ required: true })}
              />
              {errors.code && errors.code?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng nhp m bnh nhn</span>
              ) : null}
            </div>
            <div className="form-group col-md-3">
              <label>H</label>
              <input
                type="text"
                className="form-control"
                name="last_name"
                placeholder="H"
                ref={register({ required: true })}
              />
              {errors.last_name && errors.last_name?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng nhp h v tn</span>
              ) : null}
            </div>
            <div className="form-group col-md-5">
              <label>Tn</label>
              <input
                type="text"
                className="form-control form-margin"
                name="first_name"
                placeholder="Tn"
                ref={register({ required: true })}
              />
              {errors.first_name && errors.first_name?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng nhp h v tn</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Ngy sinh</label>
              <input
                type="date"
                className="form-control"
                name="birthday"
                ref={register({ required: true })}
                placeholder="Ngy sinh"
              />
              {errors.birthday && errors.birthday?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn ngy sinh</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Gii tnh</label>
              <select
                name="gender"
                className="form-control"
                placeholder="Chn gii tnh"
                ref={register({ required: true })}
              >
                <option value="">Chn gii tnh</option>
                <option value="Nam">Nam</option>
                <option value="N">N</option>
              </select>
              {errors.gender && errors.gender?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn gii tnh</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input"> Hin th nm sinh</label>
              <div className="form-check pl-0 d-flex align-items-center">
                <input
                  className="form-check-input ml-1 mr-2"
                  type="checkbox"
                  id="i-year"
                  name="birthday_year_only"
                  ref={register()}
                  style={{ marginTop: '6px' }}
                />
                <label
                  className="form-check-label ml-2"
                  htmlFor="i-year"
                  style={{ paddingLeft: '20px' }}
                >
                  {' '}
                  Ch c nm sinh
                </label>
              </div>
            </div>
            <div className="form-group col-md-4">
              <label>Trng thi</label> 
              <div className="form-check pl-0 d-flex align-items-center">
                <input
                  className="form-check-input ml-1 mr-2"
                  type="checkbox"
                  ref={register()}
                  name="is_active"
                />
                <label
                  className="form-check-label ml-2"
                  htmlFor="i-actions"
                  style={{ paddingLeft: '20px' }}
                >
                  Hot dng
                </label>
              </div>
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Tnh/Thnh ph</label>
              <select
                className="form-control"
                name="province_id"
                ref={register({ required: true })}
                onChange={({ target: { value } }) => {
                  setValue('district_id', '');
                  setValue('ward_id', '');
                  const province: any = locations.find(
                    loc => loc.province_code === value,
                  );
                  if (province) {
                    setProvince(province);
                  }
                }}
              >
                <option value="">Chn tnh</option>
                {locations?.length
                  ? locations.map((loc: any) => {
                      return (
                        <option
                          value={loc.province_code}
                          key={loc.province_code}
                        >
                          {loc.province_name}
                        </option>
                      );
                    })
                  : null}
              </select>
              {errors.province_id && errors.province_id?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn tnh/thnh ph</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Qun/Huyn</label>
              <select
                className="form-control"
                name="district_id"
                ref={register({ required: true })}
                onChange={({ target: { value } }) => {
                  setValue('ward_id', '');
                  const distSelect: any = province.districts.find(
                    dis => dis.district_code === value,
                  );
                  if (distSelect) {
                    setDist(distSelect);
                  }
                }}
              >
                <option value="">Chn qun</option>
                {!_.isEmpty(province) &&
                  province.districts.length &&
                  province.districts.map((dist, idx) => {
                    return (
                      <option key={idx} value={dist.district_code}>
                        {dist.district_name}
                      </option>
                    );
                  })}
              </select>
              {errors.district_id && errors.district_id?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn qun/huyn</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Phng/X/Th trn</label>
              <select
                className="form-control"
                name="ward_id"
                ref={register({ required: true })}
              >
                <option value="">Chn phng x</option>
                {!_.isEmpty(dist) &&
                  dist.wards.length &&
                  dist.wards.map((ward, idx) => (
                    <option value={ward.ward_code} key={idx}>
                      {ward.ward_name}
                    </option>
                  ))}
              </select>
              {errors.ward_id && errors.ward_id?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>Vui lng chn phng/x/th trn</span>
              ) : null}
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">CMND</label>
              <input
                type="text"
                className="form-control"
                placeholder="S CMND"
                name="identity_card_number"
                ref={register()}
              />
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Ngy cp</label>
              <input
                type="date"
                className="form-control"
                ref={register()}
                name="identity_card_number_issued_date"
              />
            </div>
            <div className="form-group col-md-4">
              <label htmlFor="input">Ni cp</label>
              <input
                type="text"
                className="form-control"
                placeholder="Ni cp CMND"
                ref={register()}
                name="identity_card_number_issued_place"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">S in thoi</label>
              <input
                type="tel"
                className="form-control"
                placeholder="S in thoi"
                ref={register()}
                name="phone"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">M BHYT</label>
              <input
                type="text"
                className="form-control"
                placeholder="M bo him y t"
                ref={register()}
                name="health_insurance_number"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">M n v</label>
              <input
                type="text"
                className="form-control"
                placeholder="M n v"
                ref={register()}
                name="health_insurance_first_place_code"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">Tn n v</label>
              <input
                type="text"
                className="form-control"
                placeholder="Tn n v"
                ref={register()}
                name="health_insurance_first_place"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">Ngy bt u</label>
              <input
                type="date"
                className="form-control"
                ref={register()}
                name="health_insurance_issued_date"
              />
            </div>
            <div className="form-group col-md-6">
              <label htmlFor="input">Ngy kt thc</label>
              <input
                type="date"
                className="form-control"
                ref={register()}
                name="health_insurance_expired_date"
              />
            </div>
            <div className="form-group col-md-12 d-flex justify-content-center mt-3">
              <input
                type="button"
                onClick={onClose}
                className="btn btn-secondary"
                value="ng"
              />
              <input
                type="submit"
                className="btn btn-primary ml-3"
                value="Lu thay i"
              />
            </div>
          </div>
        
        </form>
      </DialogContent>
    </Dialog>
  );
}
